@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;1,900&display=swap);
body {
  font-family: "Montserrat", sans-serif;
  text-align: center;
  padding: 0;
  margin: 0;
  background-image: linear-gradient(
    to right,
    #2b5876 0%,
    #4e4376 51%,
    #2b5876 100%
  );
  background-size: cover;
  background-repeat: no-repeat;
  height: 100vh;
}

.App {
  width: 100%;
}

#root {
  height: 100%;
}

.background {
  position: absolute;
  top: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  z-index: -2;
}

.home_container__1goC- {
  width: 100%;
  display: flex;
  flex-direction: column;
  padding-top: 10%;
  align-items: center;
  height: 85%;
}

.home_container__1goC- a {
  text-decoration: none;
  width: 350px;
  padding: 20px;
  margin: 30px;
  background: white;
  border-radius: 30px;
  color: black;
  font-weight: 500;
  font-size: 1.2em;
  opacity: 0.6;
  transition: 0.5s all;
}

.home_container__1goC- a:hover {
  opacity: 1;
}

.Navbar_navbar__3GgJF {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 15%;
}

nav {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  padding: 0px 30px;
  background: white;
  width: 80%;
  border-radius: 40px;
  margin: 20px;
}

.Navbar_image__2hydz {
  height: 60px;
  width: 100px;
  object-fit: cover;
}

nav .Navbar_name__3GWIt {
  font-weight: 600 !important;
}

